import axios from 'axios';

export namespace http {
    export const get = async (url: string, cacheString?: string) => {

        if (cacheString) {
            const cache = localStorage.getItem(cacheString); 
            if (cache !== null) {
                return JSON.parse(cache);
            } else {
                try {
                    const response = await axios.get(url);
                    localStorage.setItem(cacheString, JSON.stringify(response.data));
                    return response.data;
                } catch {
                    return [];
                }
            }
        } else {
            let response;
            try {
                response = await axios.get(url);
                return response.data;
            } catch {
                return [];
            }
        }
    }

    export const post = async (url: string, data: any) => {
        const res = await axios.post(url, data);
        return res;
    }
}