import * as React from 'react';
import { Input } from 'reactstrap';
// import Select from 'react-select';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {  getValueLabelFromEnum, ShoppingCategories, SearchModel } from 'app/utils/consts';
import { Dispatch, bindActionCreators } from 'redux';
import { SearchActions } from 'app/actions';
import { omit } from 'app/utils';
import { connect } from 'react-redux';
import { RootState } from 'app/reducers/state';
// import { push } from 'connected-react-router'
// import { connect } from 'react-redux';


const styles = require('./style.css');

const logo = require('../../../assets/images/logo.png')

interface State {
    searchText: string,
    selectedCategory: any
}



namespace TopSearchMenu {

    export interface OwnProps {
        history: any
    }
    
    export interface DispatchProps {
        searchActions: SearchActions
    }
    
    export interface StateProps {
        searchStatus: SearchModel,
    }

    export type Props = OwnProps & DispatchProps & StateProps;
    
}



class _TopSearchMenu extends React.Component<TopSearchMenu.Props, State> {
    constructor(props: TopSearchMenu.Props) {
        super(props);
        this.state = {
            searchText: '',
            selectedCategory: getValueLabelFromEnum(ShoppingCategories)[0]
        }
    }


    handleSearchChange = (e: any) => {
        this.setState({
            searchText: e.target.value
        });
    }

    handleCategoryChanged = (selectedCategory: any) => {
        this.props.searchActions.changeSearchCategory(selectedCategory.label);
        this.setState({
            selectedCategory
        });
    }

    startSearch = () => {
        this.props.searchActions.changeSearchKeywords(this.state.searchText);
        this.props.searchActions.changeSearchStatus(SearchModel.SEARCH_STARTED);
        this.props.history.push('/itemlist');
    }

    handleKeyPress = (event: any) => {
        if (event.key === 'Enter'){
            this.startSearch();
          }
    }

    handleLogoClick = () => {
        this.props.history.push('/');
    }

    render() {

        // console.log(window.location.pathname.split('/')[1] === "")
        const isMainPage = window.location.pathname.split('/')[1] === "";

        const logoStyle = isMainPage ? {display: "block", margin: "auto"} : {}; 
        const searchBoxStyle = isMainPage ? {position: 'relative' as 'relative', display: "block", margin: "auto", top: "auto", width: "50%"} : {};

        const containerStyle = this.props.searchStatus === SearchModel.WAITING_FOR_SEARCH ? {} : {};
        return (
            <div className={styles.container} style={containerStyle}>
                <div className={styles.logoContainer} onClick={this.handleLogoClick} style={logoStyle}>
                    <img src={logo} className={styles.logo} />
                </div>
                <div className={styles.searchBox} style={searchBoxStyle}>
                    <Input value={this.state.searchText} onKeyPress={this.handleKeyPress} onChange={this.handleSearchChange} className={styles.inputSearch} type="text" placeholder="search product by name" />
                    {/* <Select onChange={this.handleCategoryChanged} options={getValueLabelFromEnum(ShoppingCategories)} value={this.state.selectedCategory} className={styles.inputSelect} /> */}
                    <div className={styles.searchIcon} onClick={this.startSearch}>
                        <FontAwesomeIcon icon="search" />
                    </div>                    
                </div>
                {/* <div className={styles.moreDetails}>
                    <FontAwesomeIcon icon="heart"  className={styles.heartIcon} />
                    <FontAwesomeIcon icon="shopping-cart"  className={styles.cartIcon} />
                </div> */}
            </div>
        )
    }
}

const mapStateToProps = (state: RootState): Pick<TopSearchMenu.StateProps, 'searchStatus'> => {
    return {searchStatus: state.search.searchStatus};
  };

const mapDispatchToProps = (dispatch: Dispatch): Pick<TopSearchMenu.Props, 'searchActions'> => ({
    searchActions: bindActionCreators(omit(SearchActions, 'Type'), dispatch)
});

export const TopSearchMenu = connect<TopSearchMenu.StateProps, TopSearchMenu.DispatchProps, TopSearchMenu.Props>(mapStateToProps, mapDispatchToProps)(_TopSearchMenu as any);