import * as React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { ItemModel } from 'app/models/Items';
import { AppData } from 'app/utils/data';
import ReactTooltip from 'react-tooltip'
import Badge from 'reactstrap/lib/Badge';
import Button from 'reactstrap/lib/Button';
import { setIsItemSimilar } from 'app/utils/items';



const styles = require('./style.css');

interface State {
    isUserVotedForSimilar: boolean
}

interface OwnProps {
    item: ItemModel,
    onItemImageSelected: (itemId: number) => void,
    history: any,
    isSimilarItem?: boolean,
    similarItemRank? :number,
}

type Props = OwnProps;

class Item extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            isUserVotedForSimilar: false
        }
    }

    openBigImage = (itemId: number) => {
        return () => {
            this.props.onItemImageSelected(itemId);
        }
    }

    handleGetItem = () => {
        if (this.props.isSimilarItem) {
            window.open(this.props.item.itemURL, '_blank');
        } else {
            AppData.selectedItem = this.props.item;
            this.props.history.push(`/item/${this.props.item.store}/${this.props.item.id}`);
        }
        
    }

    handleSetITemSimilar = async () => {
        await setIsItemSimilar(this.props.item.id, this.props.item.store, true);
        this.setState({
            isUserVotedForSimilar: true
        })
    }

    handleSetITemNotSimilar = async () => {
        await setIsItemSimilar(this.props.item.id, this.props.item.store, false);
        this.setState({
            isUserVotedForSimilar: true
        })
    }


    render() {
        // const fakeArray = [];
        // for(let i=0; i<this.props.item.reviewAverage; i++) {
        //     fakeArray.push(1)
        // }
        // const itemImage = this.props.isRelatedItem ? this.props.item.bigImageUrl : this.props.item.smallImageUrl;
        console.log(`this.props.similarItemRank`, this.props.similarItemRank)
        const itemImage = this.props.item.bigImageUrl || this.props.item.smallImageUrl;
        const imageStyle = {height: "100%", margin: "auto"};
        return (
            <>
            <div className={styles.gridItemContainer}>
                {!this.props.isSimilarItem ? 
                    <div className={styles.gridItemHeader}>
                        {this.props.item.condition}
                    </div>
                    :
                    <div className={styles.similarItemRank}>
                        {this.props.similarItemRank === 0 && 
                        <>
                            <FontAwesomeIcon icon="medal" color={'#C0C0C0'} size={'lg'} />
                            Second Best Offer
                        </> }
                        {this.props.similarItemRank === 1 && 
                        <>
                            <FontAwesomeIcon icon="medal" color={'#b08d57'} size={'lg'} className={styles.heartIcon} />
                            Third Best Offer
                        </> }
                        {this.props.similarItemRank !== 1 && this.props.similarItemRank !== 0 && this.props.similarItemRank !== undefined &&
                            <>
                                #{this.props.similarItemRank+1} Offer Rank
                            </>
                        }
                    </div>
                }
                {this.props.isSimilarItem &&
                    <div className={styles.checkAlgorithm}>
                        {!this.state.isUserVotedForSimilar ?
                        <>
                            Is this item similar? <br />
                            <Button color="info" style={{marginRight: "5px"}} onClick={this.handleSetITemSimilar}>
                                <FontAwesomeIcon icon="thumbs-up" color={'white'} />
                            </Button>
                            <Button color="danger" style={{marginLeft: "5px"}} onClick={this.handleSetITemNotSimilar}>
                                <FontAwesomeIcon icon="thumbs-down" color={'white'} />
                            </Button>
                        </>
                        :
                        <>
                            <h5>
                                <FontAwesomeIcon icon="smile-wink" color={'black'} />Thanks!
                                {/* <Badge color="dark"><FontAwesomeIcon icon="laugh-wink" color={'white'} />Thanks!</Badge> */}
                            </h5>
                        </>    
                    }
                        
                    </div>
                }
{/* 
                {this.props.similarItemRank && this.props.isSimilarItem && 
                    <div className={styles.similarItemRank}>

                    </div>
                } */}
                <div className={styles.gridImageContainer} onClick={this.handleGetItem}>
                    <img src={`${itemImage}`} style={imageStyle} />
                </div>
                <div className={styles.gridItemTitle} data-tip={this.props.item.title}>
                    {this.props.item.title}
                </div>
                <ReactTooltip />
                <div className={styles.gridItemPrice}>
                    <span className={styles.gridItemPriceMain}>{this.props.item.price}$</span> 
                    <span className={styles.gridItemPriceShipping}>( + {this.props.item.shippingPrice}$ Shipping )</span>
                </div>
                <div className={styles.gridItemRating}>
                    {/* <div className={styles.gridItemRatingStart}>
                        {fakeArray.map((value, index) => <FontAwesomeIcon key={index} icon="star" color={'#eded34'} className={styles.heartIcon} />)}
                    </div> */}
                    {!this.props.isSimilarItem && 
                        <div className={styles.gridItemRatingDetails}>
                            {/* ({this.props.item.reviews && this.props.item.reviews.length} reviews) */}
                            {/* {this.props.item.isItemBest && <><br /><span>best</span></>} */}
                            {/* <br /> */}
                            {this.props.item.relatedItems && this.props.item.relatedItems.length > 0 && 
                                <Badge color="info">
                                    {this.props.item.relatedItems.length} Similar Items
                                </Badge>
                            }
                        </div>
                    }
                    <div className={styles.gridItemMoreDetails} style={this.props.isSimilarItem ? {textAlign: 'center', top: 0} : {}}>
                        {!this.props.isSimilarItem &&
                            <div className={styles.gridItemMoreDetailsOpenItemPicture} onClick={this.openBigImage(this.props.item.id)}>
                                <FontAwesomeIcon icon="search-plus" color={'#a5a5a5'} size={'lg'} className={styles.heartIcon} />
                            </div>
                        }
                        
                        
                        <div className={styles.gridItemMoreDetailsOpenItem} onClick={this.handleGetItem}>
                            Get Item!
                        </div>
                        
                    </div>
                </div>
            </div>
            </>
        )
    }
}

export default Item;