import * as React from 'react';
import Popup from "reactjs-popup";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const styles = require('./style.css');

interface Props {
    imageList: any[]
}

interface State {
    currentImageId: number,
    isImageOpen: boolean
}

export class ImageGallery extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            currentImageId: 0,
            isImageOpen: false
        }
    }

    selectImageByIndex = (index: number) => {

        this.setState({
            currentImageId: index
        })
    }

    onSelectNextImage = () => {
        if (this.state.currentImageId !== this.props.imageList.length - 1) {
            this.setState({currentImageId: this.state.currentImageId+1})
        }
    }

    onSelectPreviousImage = () => {
        if (this.state.currentImageId !== 0) {
            this.setState({currentImageId: this.state.currentImageId-1})
        }
    }

    render() {
        let allImages = this.props.imageList;
        if (allImages.length > 4) allImages.length = 4;
        const bigImage = allImages.find((image: any, index: number) => index === this.state.currentImageId);
        const rightArrowStyle = this.state.currentImageId === (this.props.imageList.length - 1) ? {opacity: 0.4} : {};
        const leftArrowStyle = this.state.currentImageId === 0 ? {opacity: 0.4} : {};
        // const otherImages = allImages.filter((image: any, index: number) => index !== this.state.currentImageId)
        return (
            <div className={styles.container}>
                
                <div className={styles.bigPictureContainer}>    
                <div className={styles.rightArrowContainer} onClick={this.onSelectNextImage}>
                    <FontAwesomeIcon icon="arrow-alt-circle-right" color={"#4c4c4c"} style={rightArrowStyle} /> 
                </div>
                <div className={styles.leftArrowContainer} onClick={this.onSelectPreviousImage}>
                    <FontAwesomeIcon icon="arrow-alt-circle-left" color={"#4c4c4c"} style={leftArrowStyle} /> 
                </div>
                <Popup modal trigger={
                    <img src={bigImage} style={{width: "100%", marginLeft: "50%", marginTop: "50%",transform: "translateX(-50%) translateY(-50%)", cursor: "pointer"}} />
                }>
                    <div>
                        <img src={bigImage} />
                    </div>
                </Popup>
                    
                </div>
                <div className={styles.smallImageContainer}>
                    {allImages.map((image: any, index: number) => {
                        // if (index !== this.state.currentImageId) {
                            return (
                                <div style={{width: "23%", marginLeft: "1%", marginRight: "1%",  display: "inline-block", height: "60px", backgroundColor: "white"}}>
                                    <img src={image} style={{marginLeft: "50%", transform: "translateX(-50%)", width: "100%", cursor: "pointer"}} onClick={() => this.setState({currentImageId: index})} />
                                </div>
                            )
                        // }
                        // return;
                    })}
                </div>
            </div>
        )
    }
}